body {
  font-family: Poppins, sans-serif;
  font-size: 24px;
  line-height: 1.5;
  text-align: center;
  color: #333;
  margin: 0;
}

h1 {
  font-size: 64px;
  margin-bottom: 0;
}

p {
  font-size: 32px;
}

h2 {
  font-size: 52px;
}

a {
  color: #477ca7;
}

a:visited {
  color: #74638f;
}

#main {
  margin: 30px 8px;
  padding: 15px;
  border-radius: 5px;
  background: #eee;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
  margin: 0 auto;
}

#img-div {
  background: #eee;
  padding: 10px;
  margin: 0;
}

#img-caption {
  margin: 15px 0 5px;
}

#headline {
  margin: 50px 0;
  text-align: center;
}

section {
  display: block;
}

h3 {
  display: block;
  font-size: 32px;
  font-weight: 700;
}

ul {
  max-width: 550px;
  margin: 0 auto 50px;
  text-align: left;
  line-height: 1.6;
  display: block;
}

li {
  margin: 16px 0;
  display: list-item;
}

blockquote {
  font-style: italic;
  max-width: 545px;
  margin: 0 auto 50px;
  text-align: left;
}

@media (max-width: 460px) {
  h1 {
    font-size: 44px;
    line-height: 1.2;
  }
  li {
    font-size: 18px;
  }
  p,
  h3 {
    font-size: 24px;
  }
  #img-caption {
    font-size: 18px;
  }
  #main {
    margin: 5px;
  }
}
